.component-main-container{
    width: calc(100% - 1px); 
    overflow: auto; 
    display: flex;
    justify-content: center; 
}

.component-main-contents-container{
    list-style-type: none;
    padding-left: 5px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: calc(100% - 1px); 
}

.component-el{
    display:flex;
    align-items: center;
    justify-content: space-between;
    width: inherit;
    cursor: pointer;
    padding:5px;
    border-radius: 4px;
}

.component-el:hover{
    background-color: rgba(218, 218, 218, 0.288);
}

.label-container{
    font-size:11px;
    color: rgb(49, 51, 63, .5);
    font-family: "Source Sans Pro", sans-serif;
    font-weight: 400;
    line-height:1.6;
}

.match-container{
    font-size:11px;
    color: rgb(49, 51, 63, .5);
    font-family: "Source Sans Pro", sans-serif;
    font-weight: 400;
    line-height:1.6;
}

